#team .our-team{
    text-align: center;
    transition: all 0.5s ease 0s;
}
#team .our-team:hover{
    box-shadow: 0 15px 10px -10px rgba(0, 0, 0, 0.5), 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
}
#team .our-team .pic{
    overflow: hidden;
    position: relative;
}
#team .our-team .pic:before,
#team .our-team .pic:after{
    content: "";
    width: 200%;
    height: 80%;
    background: rgba(38,37,37,0.8);
    position: absolute;
    top: -100%;
    left: -4%;
    transform: rotate(45deg);
    transition: all 0.5s ease 0s;
}
#team .our-team .pic:after{
    background: rgba(69, 69, 69, 0.37);
    top: auto;
    left: auto;
    bottom: -100%;
    right: -4%;
}
#team .our-team:hover .pic:before{ top: 0; }
#team .our-team:hover .pic:after{ bottom: 0; }
#team .our-team .pic img{
    width: 100%;
    height: auto;
}
#team .our-team .social{
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    position: absolute;
    bottom: 45%;
    left: 0;
    opacity: 0;
    z-index: 2;
    transition: all 0.5s ease 0.3s;
}
#team .our-team:hover .social{ opacity: 1; }
#team .our-team .social li{ display: inline-block; }
#team .our-team .social li i{
    display: block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    font-size: 20px;
    color: #fff;
    margin-right: 10px;
    position: relative;
    transition: all 0.3s ease 0s;
}
#team .our-team .social li i:after{
    content: "";
    width: 100%;
    height: 100%;
    background: #0a0a0a;
    border-radius: 0 20px 20px 20px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: all 0.3s ease 0s;
}
#team .our-team .social li i:hover:after{ transform: rotate(180deg); }
#team .our-team .team-content{ padding: 20px; }
#team .our-team .title{
    font-size: 22px;
    font-weight: 700;
    letter-spacing: 2px;
    color: #202021;
    text-transform: uppercase;
    margin-bottom: 7px;
}
#team .our-team .post{
    display: block;
    font-size: 17px;
    font-weight: 600;
    color: #707070;
    text-transform: capitalize;
}
@media only screen and (max-width: 990px){
    #team .our-team{ margin-bottom: 30px; }
}

